	margin-top   : 10px;
	margin-bottom: 10px;
}

.notifyimg {
	color         : $white;
	float         : left;
	height        : 40px;
	line-height   : 35px;
	margin-right  : 10px;
	text-align    : center;
	vertical-align: middle;
	width         : 40px;
	border-radius : 50%;
}

.app-selector ul {
