}

.block {
	display: block !important;
}

.floating {
	animation  : floating 3s ease infinite;
	will-change: transform;
}

/*----- Global Loader -----*/

#global-loader {
	position  : fixed;
	z-index   : 50000;
	background: $white;
	left      : 0;
	top       : 0;
	right     : 0;
	bottom    : 0;
	height    : 100%;
	width     : 100%;
	margin    : 0 auto;
	overflow  : hidden;
}

.loader-img {
	position  : absolute;
	left      : 0;
	right     : 0;
	text-align: center;
	top       : 35%;
	margin    : 0 auto;
}


/*----- Global Loader -------*/

/*------ Navigation -------*/

ul {
	li ul:hover {
		visibility: visible;
		opacity   : 1;
		display   : block;
	}

	&.labels-info li {
		h4 {
			color         : #5c5c5e;
			font-size     : 13px;
			padding-left  : 15px;
			padding-right : 15px;
			padding-top   : 5px;
			text-transform: uppercase;
		}

		margin: 0;

		a {
			border-radius: 0;
			color        : #6a6a6a;

			&:hover,
			&:focus {
				background: none repeat scroll 0 0 #d5d7de;
				color     : #6a6a6a;
			}

			i {
				padding-right: 10px;
			}
		}
	}

	list-style-type: none;
	padding        : 0px;
	margin         : 0px;
}



/*------ Email services -------*/

#copy {
	background : none;
	color      : $white;
	font-weight: bold;
	padding    : 8px 25px;
	border     : 0;
}

/*------ Embed-responsive -------*/

.embed-responsive {
	position: relative;
	display : block;
	width   : 100%;
	padding : 0;
	overflow: hidden;

	&::before {
		display: block;
		content: "";
	}

	.embed-responsive-item,
	iframe,
	embed,
	object,
	video {
		position: absolute;
		top     : 0;
		bottom  : 0;
		left    : 0;
		width   : 100%;
		height  : 100%;
		border  : 0;
	}
}

.embed-responsive-21by9::before {
	padding-top: 42.85714286%;
}

.embed-responsive-16by9::before {
	padding-top: 56.25%;
}

.embed-responsive-4by3::before {
	padding-top: 75%;
}

.embed-responsive-1by1::before {
	padding-top: 100%;
}

.fixed-top {
	position: fixed;
	top     : 0;
	right   : 0;
	left    : 0;
	z-index : 1030;
}

.fixed-bottom {
	position: fixed;
	right   : 0;
	bottom  : 0;
	left    : 0;
	z-index : 1030;
}

@supports (position: -webkit-sticky) or (position: sticky) {
	.sticky-top {
		position: -webkit-sticky;
		position: sticky;
		top     : 0;
		z-index : 1020;
	}
}

.sr-only {
	position   : absolute;
	width      : 1px;
	height     : 1px;
	padding    : 0;
	overflow   : hidden;
	clip       : rect(0, 0, 0, 0);
	white-space: nowrap;
	border     : 0;
}

.sr-only-focusable {

	&:active,
	&:focus {
		position   : static;
		width      : auto;
		height     : auto;
		overflow   : visible;
		clip       : auto;
		white-space: normal;
	}
}

.shadow-sm {
	box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075) !important;
}

.shadow {
	box-shadow: 0 0.5rem 1rem $black-15 !important;
}

.shadow-lg {
	box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175) !important;
}

.shadow-none {
	box-shadow: none !important;
}

.visible {
	visibility: visible !important;
}

.invisible {
	visibility: hidden !important;
}

@media print {
	* {
		text-shadow: none !important;
		box-shadow : none !important;

		&::before,
		&::after {
			text-shadow: none !important;
			box-shadow : none !important;
		}
	}

	a:not(.btn) {
		text-decoration: underline;
	}

	abbr[title]::after {
		content: " ("attr(title) ")";
	}

	pre {
		white-space      : pre-wrap !important;
		border           : 1px solid #adb5bd;
		page-break-inside: avoid;
	}

	blockquote {
		border           : 1px solid #adb5bd;
		page-break-inside: avoid;
	}

	thead {
		display: table-header-group;
	}

	tr,
	img {
		page-break-inside: avoid;
	}

	p,
	h2,
	h3 {
		orphans: 3;
		widows : 3;
	}

	h2,
	h3 {
		page-break-after: avoid;
	}

	@page {
		size: a3;
	}

	body,
	.container {
		min-width: 992px !important;
	}

	.navbar {
		display: none;
	}

	.badge {
		border: 1px solid $black;
	}

