/*--banners--*/

.banner1 {
	.carousel-inner img {
		width     : 100%;
		max-height: 460px;
	}

	.carousel-item:before {
		content   : '';
		display   : block;
		position  : absolute;
		background: $black-6;
		width     : 100%;
		height    : 100%;
		right     : 0;
		top       : 0;
	}

	.carousel-control {
		width: 0;

		&.left,
		&.right {
			opacity          : 1;
			filter           : alpha(opacity=100);
			background-image : none;
			background-repeat: no-repeat;
			text-shadow      : none;
		}

		&.left span,
		&.right span {
			padding: 15px;
		}

		.glyphicon-chevron-left,
		.glyphicon-chevron-right,
		.icon-prev,
		.icon-next {
			position: absolute;
			top     : 45%;
			z-index : 5;
			display : inline-block;
		}

		.glyphicon-chevron-left,
		.icon-prev {
			left: 0;
		}

		.glyphicon-chevron-right,
		.icon-next {
			right: 0;
		}

		&.left span,
		&.right span {
			background: $white-1;
			color     : $white;
		}

		&.left span:hover,
		&.right span:hover {
			background: $white-3;
		}
	}

	.header-text {
		position: absolute;
		left    : 0;
		right   : 0;
		color   : $white;
	}

	.slider .header-text {
		top: 29%;
	}

	.header-text {
		h2 {
			font-size: 40px;
		}

		h3 {
			font-size: 25px;
		}

		h2 span {
			padding: 10px;
		}

		h3 span {
			padding: 15px;
		}
	}
}

